.nav-header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: var(--z-header);
  width: 100%;
  height: var(--header-height);
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  place-items: center;
  gap: 10px;
  transition: height var(--theme-duration-1), background var(--theme-duration-1);
}
.nav-header::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  transition: opacity var(--theme-duration-1);
  opacity: 0;
}
.nav-header.nav-active {
  height: var(--header-active-height);
}
.nav-header.nav-active::before {
  opacity: 1;
}
.nav-header.nav-active .nav-logo img {
  filter: brightness(0) invert(1);
}
.nav-menu-btn {
  position: fixed;
  left: var(--side-margin);
  top: var(--header-menu-top);
  width: 22px;
  height: 22px;
  cursor: pointer;
  transition: top var(--theme-duration-1);
  z-index: calc(var(--z-header) + 1);
}
.nav-header.nav-active .nav-menu-btn {
  top: var(--header-active-menu-top);
}
.nav-logo {
  position: fixed;
  left: 50%;
  top: var(--header-logo-top);
  transform: translateX(-50%);
  width: 60px;
  z-index: calc(var(--z-header) + 1);
  transition: top var(--theme-duration-1), width var(--theme-duration-1);
}
.nav-header.nav-active .nav-logo {
  top: var(--header-active-logo-top);
  width: 35px;
}
.nav-tel {
  position: fixed;
  top: var(--header-logo-top);
  right: var(--side-margin);
  width: 20px;
  height: 20px;
  z-index: calc(var(--z-header) + 1);
  transition: top var(--theme-duration-1);
}
.nav-header.nav-active .nav-tel {
  top: var(--header-active-logo-top);
}
@media (min-width: 1024px) {
  .nav-header.nav-with-sidebar .nav-menu-btn {
    left: calc(var(--sidebar-w) + var(--side-margin));
  }
  .nav-header.nav-with-sidebar .nav-logo {
    left: calc(var(--sidebar-w) + (100% - var(--sidebar-w)) / 2);
    transform: translateX(-50%);
  }
}
@media (max-width: 500px) {
  .nav-header {
    height: var(--header-active-height);
  }
