._lifting .feature .tit {
  text-align: center;
  margin-bottom: 112px;
}
._lifting .feature .tit h2 {
  font-size: clamp(40px, 6vw, 60px);
  font-family: var(--font-en-display);
  font-weight: 300;
  color: #111;
  margin-bottom: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
._lifting .feature .tit p {
  font-size: clamp(18px, 2.2vw, 24px);
  color: #6b6b6b;
  font-weight: 300;
  letter-spacing: 0.02em;
}
._lifting .feature .tit p span {
  font-weight: 400;
  color: #2c2c2c;
}
._lifting .feature .con {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
  margin-bottom: 64px;
}
@media (max-width: 768px) {
  ._lifting .feature .con {
    grid-template-columns: 1fr;
  }
}
._lifting .feature .con .bx {
  background: white;
  padding: 40px;
  border-radius: 2px;
  border: 1px solid #e5e0d8;
  transition: all 0.5s ease;
}
._lifting .feature .con .bx:hover {
  border-color: #d4c4a0;
  box-shadow: 0 8px 30px rgba(212, 196, 160, 0.15);
}
._lifting .feature .con .bx .bxCont .top p {
  font-size: 24px;
  font-weight: 300;
  color: #2c2c2c;
  margin-bottom: 16px;
  letter-spacing: 0.03em;
}
._lifting .feature .con .bx .bxCont .top p .num {
  color: #b8a078;
  margin-right: 12px;
  font-weight: 300;
}
._lifting .feature .con .bx .bxCont .top span {
  font-size: 15px;
  color: #6b6b6b;
  line-height: 1.85;
  display: block;
  font-weight: 300;
  letter-spacing: 0.01em;
}
._lifting .feature .con .bx .bxCont .bottom .view {
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--hm-gold);
  opacity: 0;
  transition: opacity 0.3s ease;
}
._lifting .feature .con .bx:hover .bxCont .bottom .view {
  opacity: 1;
}
._lifting .feature .linkBx {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
}
._lifting .feature .linkBx a {
  padding: 12px 24px;
  border: 1px solid #ddd;
  border-radius: 9999px;
  font-size: 14px;
  transition: all 0.3s ease;
  background: white;
  color: #666;
}
._lifting .feature .linkBx a:hover {
  border-color: #111;
  color: #111;
}
._lifting .feature .linkBx a.on {
  background: #111;
  color: white;
  border-color: #111;
}
@media (max-width: 1024px) {
  ._lifting .horizontal .Wrap {
    flex-direction: column;
    width: 100%;
  }
  ._lifting .sec1,
  ._lifting .sec2,
  ._lifting .sec3 {
    width: 100%;
    height: auto;
    min-height: 100vh;
  }
}
